import NavTaskContainer from "./nav/navTaskContainer"
import { Route } from 'react-router-dom';
import logo from '../../logo.svg';
import Step1Container from "./step1/step1Container";
import Step2Container from "./step2/step2Container";
import Step3Container from "./step3/step3Container";
import Step4Container from "./step4/step4Container";
import Step5Container from "./step5/step5Container";
import AllSteps from "./allSteps/allSteps";
import ListTaskContainer from "./listTask/listTaskContainer";
import React, { Component } from 'react';
import { connect } from "react-redux";
import { getStatusUser } from "../../redux/reducers/reducer_taskDO";
import ListServicesByGid from "./listServicesByGid/listServicesByGidContainer";
import TestForm from "./testForm/testForm";
import TestForm2 from "./testForm/testForm2";

class AddTaskDoStart extends Component {
    componentDidMount() {
        this.props.getStatusUser();
    }
    
    render() {
        //console.log(this.props)
        let listServicesByGid=this.props.TaskDOForm.statusUser=='admin'?<ListServicesByGid />:'';
        return (
            <div>
                <Route path="/react/AddTaskDO/:step?/:idtask?" render={() => <NavTaskContainer />} />
                <img src={logo} className="App-logo float-right mb-2" alt="logo" />
                <Route path="/react/AddTaskDO/step1/:idtask?" render={() => <div><Step1Container />{listServicesByGid}</div>} />
                <Route path="/react/AddTaskDO/step2/:idtask?" render={() => <div><Step2Container />{listServicesByGid}</div>} />
                <Route path="/react/AddTaskDO/step3/:idtask?" render={() => <div><Step3Container />{listServicesByGid}</div>} />
                <Route path="/react/AddTaskDO/step4/:idtask?" render={() => <div><Step4Container />{listServicesByGid}</div>} />
                <Route path="/react/AddTaskDO/step5/:idtask?" render={() => <div><Step5Container />{listServicesByGid}</div>} />
                <Route path="/react/AddTaskDO/allSteps/:idtask?" render={() => <div><AllSteps />{listServicesByGid}</div>} />
                <Route path="/react/AddTaskDO/listTask" render={() => <ListTaskContainer />} />
                <Route path="/react/AddTaskDO/testForm" render={() => <TestForm />} />
                <Route path="/react/AddTaskDO/testForm2" render={() => <TestForm2 />} />
            </div>
        )
    }
}



let mapStateToProps = (state) => {
    //debugger;
    return { ...state }
}


export default connect(mapStateToProps, { getStatusUser })(AddTaskDoStart)