const UPDATEDIRS = 'updateDirs'

function directoryesReducer(state = {}, action) {
    //debugger;
    switch (action.type) {
        case UPDATEDIRS:
            var newState={
                ...state,
                dir_executors:[...action.data.dir_executors],
                dir_tyteTasks:[...action.data.dir_tyteTasks]
            };
            return newState;
        default:
            return state;
    }

}
export default directoryesReducer;