function UpdateTask(props) {
  //debugger;

  let executors=props.dirExecutors?props.dirExecutors.map(item=><option kay={item.id} value={item.id}>{item.name}</option>):[];
  let tyteTasks=props.dirTyteTasks?props.dirTyteTasks.map(item=><option kay={item.id} value={item.id}>{item.name}</option>):[];

  return (
    <div>
      <form>
        <h5 className='d-inline-block'> </h5>
        <div className="mb-3">
          <label className="form-label"> </label>
          <select className="form-control" value={props.dataTask.executor_id} onChange={props.callToUpdateTask} data-name='executor_id' data-id={props.dataTask.id}>
            <option></option>
            {executors}
          </select>
        </div>
        <div className="row">
          <div className="col-6">
            <div className="mb-3">
              <label className="form-label"> </label>
              <textarea className="form-control" rows="3"
                onChange={props.callToUpdateTask} data-name='task' data-id={props.dataTask.id} value={props.dataTask.task}></textarea>
            </div>
          </div>
          <div className="col-6">
            <div className="mb-3">
              <label className="form-label"> </label>
              <textarea className="form-control" rows="3"
                onChange={props.callToUpdateTask} data-name='result' data-id={props.dataTask.id} value={props.dataTask.result}></textarea>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-4">
            <label className="form-label">  </label>
            <input type="date" className="form-control"
              onChange={props.callToUpdateTask} data-name='dateBegeen' data-id={props.dataTask.id}
              value={props.dataTask.dateBegeen}></input>
          </div>
          <div className="col-4">
            <label className="form-label"> </label>
            <input type="date" className="form-control"
              onChange={props.callToUpdateTask} data-name='dateEnd' data-id={props.dataTask.id}
              value={props.dataTask.dateEnd}></input>
          </div>
          <div className="col-4">
            <label className="form-label"> </label>
            <select className="form-control"
              onChange={props.callToUpdateTask} data-name='tyteTask_id' data-id={props.dataTask.id}
              value={props.dataTask.tyteTask_id}>
              <option></option>
              {tyteTasks}
            </select>
          </div>
        </div>
        <div className="mb-3">
          <label className="form-label"> </label>
          <textarea className="form-control" rows="3"
            onChange={props.callToUpdateTask} data-name='textComplete' data-id={props.dataTask.id} value={props.dataTask.textComplete}></textarea>
        </div>

        <CheckRadio changeStatus={props.changeStatus} id={props.dataTask.id} value='1' valArr={props.dataTask.statusTask} name=''/>
        <CheckRadio changeStatus={props.changeStatus} id={props.dataTask.id} value='-1' valArr={props.dataTask.statusTask} name=''/>
        <CheckRadio changeStatus={props.changeStatus} id={props.dataTask.id} value={null} valArr={props.dataTask.statusTask} name=' '/>

        
        <button className="btn btn-success w-100 mt-2" data-id={props.dataTask.id} onClick={props.saveItem}></button>
        
      </form>

    </ div >
  )
}

function CheckRadio(props) {
  //debugger;
  var checked = props.value == props.valArr ? true : false;
  return (
    <div className="form-check form-check-inline">
      <label className="form-check-label">
        <input onChange={props.changeStatus} name='statustask' className="form-check-input" type="radio" kay={props.id} data-id={props.id} checked={checked} data-statustask={props.value}></input>
        {props.name}</label>
    </div>
  )
}

export default UpdateTask;