import Step3 from "./step3";
import { connect } from "react-redux";
import { withRouter } from "react-router";
import { compose } from "redux";
import {
    toggleAddServiceFormAC, getPeriodsAct, changeDataAC, getServices,
    addServicesListSelectedAC, getEOSZ, deleteServicesListSelectedAC, defaultSubjectAC,getTask,touchedAC
} from "../../../redux/reducers/reducer_taskDO";


let mapStateToProps = (state) => {
    //debugger;
    return {
        addServiceFormToggle: state.TaskDOForm.toggleForms.addServiceForm,
        periodsActList: state.TaskDOForm.periodsActList,
        servicesList: state.TaskDOForm.servicesList,
        temporaryTaskDO: state.TaskDOForm.temporaryTaskDO,
        servicesListSelected: state.TaskDOForm.servicesListSelected,
        EOSZList: state.TaskDOForm.EOSZList,
        errors: state.TaskDOForm.errors,
        currentTeask: state.TaskDOForm.currentTeask
    }
}

let Step3Container = compose(
    connect(mapStateToProps,
        {
            toggleAddServiceFormAC, getPeriodsAct, changeDataAC, getServices,
            addServicesListSelectedAC, getEOSZ, deleteServicesListSelectedAC, defaultSubjectAC,getTask,touchedAC
        }),
    withRouter)
    (Step3);
export default Step3Container