import { connect } from "react-redux";
import UpdateTask from "./updateTask";
import { Route } from 'react-router-dom';
import { UpdateTaskAC, changeStatusAC, saveItemAC } from "../../../redux/reducers/reducer_ListTask";

function ContainerRouteUpdateTask(props) {
    //debugger;
    return <Route path="/ReadTask" render={() =>
        <RouteUpdateTask
            changeStatus={props.changeStatus}
            callToUpdateTask={props.callToUpdateTask}
            saveItem={props.saveItem}
            dataTasks={props.dataTasks}
            dirExecutors={props.dirExecutors}
            dirTyteTasks={props.dirTyteTasks} />} />
}


let mapStateToProps = (state) => {
    return {
        dataTasks: state.arrTasks.arrTasks,
        dirExecutors: state.directoryes.dir_executors,
        dirTyteTasks: state.directoryes.dir_tyteTasks
    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        saveItem: function (e) {
            e.preventDefault();
            var action = saveItemAC({
                id: e.target.getAttribute('data-id')
            });
            action.dispatch = dispatch;
            dispatch(action)
        },
        changeStatus: function (e) {
            dispatch(
                changeStatusAC({
                    data: e.target.getAttribute('data-statustask'),
                    name: 'statusTask',
                    id: e.target.getAttribute('data-id')
                })
            )
        },
        callToUpdateTask: function (e) {
            dispatch(
                UpdateTaskAC(
                    e.target.value,
                    e.target.getAttribute('data-name'),
                    e.target.getAttribute('data-id')
                )
            )
        }
    }
}

let ContainerUpdateTask = connect(mapStateToProps, mapDispatchToProps)(ContainerRouteUpdateTask);

function RouteUpdateTask(props) {
    //debugger;
    if (!props.dataTasks) {
        return <div></div>
    }
    let url = window.location.href;
    let idTask = url.split('/');
    idTask = idTask[idTask.length - 1];
    let task = props.dataTasks.find(item => item.id == idTask);
    if (!task) return <div></div>;

    return (
        <UpdateTask changeStatus={props.changeStatus}
            callToUpdateTask={props.callToUpdateTask}
            saveItem={props.saveItem}
            dataTask={task}
            dirExecutors={props.dirExecutors}
            dirTyteTasks={props.dirTyteTasks} />
    )

}



export default ContainerUpdateTask;