import React, { Component } from 'react';
import { connect } from "react-redux";
import { changeDataAC,gotoWork,cancelTask } from "../../../redux/reducers/reducer_taskDO";
import { MyChekBoxKPP, MyTextAreaKPP, MyInputFileKPP } from "../inputs/inputs";

class AdminForm extends Component {
    changeData(e) {
        /* const file = event.target.files[0];
        let formData = new FormData();
        formData.append('file', file);
        //console.log(formData)
        //Make a request to server and send formData */
        //debugger;
        this.props.changeDataAC({
            name: e.target.getAttribute('data-name'),
            data: e.target.files
        })
    }
    validationRequired = (name) => {
        //debugger
        var find = !(this.props.errors.allInputs.findIndex(item => item == name) == -1)
        //console.log(name+': '+find)
        if(name=='FileSU'){
            console.log(name+' touched: '+this.props.errors[name].touched)
            console.log(name+' error: '+this.props.errors[name].error)
        }
        
        return find && this.props.errors[name].touched && this.props.errors[name].error
        
    }
    touched = (name) => {
        //debugger
        return () => this.props.touchedAC(name)
    }
    changeData=(e)=> {
        this.props.changeDataAC({
            name: e.target.getAttribute('data-name'),
            data: e.target.value
        })
    }
    render() {
        //console.log(this.props)
        let users=this.props.listUsersPP.map(item=><option key={item.id} value={item.id}>{item.name}</option>)
        return (
            <div>
                <div className='form-group'>
                    <label><h6> p  </h6></label>
                    <select className="form-control"
                        data-name='IdAuthor' value={this.props.IdAuthor} onChange={this.changeData}>
                        {users}
                    </select>
                </div>
                <div className='form-group'>
                    <label><h6>ID  </h6></label>
                    <input className="form-control"
                        data-name='IDPK' value={this.props.IDPK} onChange={this.changeData} />
                </div>
                <MyInputFileKPP 
                {...this.props} 
                    title='  :' 
                    name='aditionalFilesDO'
                    onChange={this.changeData}
                    onBlur={this.touched}
                    validation={this.validationRequired}
                />
                <div>
                    <button className='btn btn-primary w-100' onClick={this.props.gotoWork}>  </button>
                    <button className='btn btn-danger w-100 mt-2' onClick={this.props.cancelTask}></button>
                </div>
            </div>
        );
    }
}



let mapStateToProps = (state) => {
    //debugger;
    return {
        temporaryTaskDO: state.TaskDOForm.temporaryTaskDO,
        listUsersPP: state.TaskDOForm.listUsersPP,
        IdAuthor: state.TaskDOForm.temporaryTaskDO.IdAuthor,
        errors: state.TaskDOForm.errors
    }
}
export default connect(mapStateToProps,{changeDataAC,gotoWork,cancelTask})(AdminForm);
