import { NavLink } from "react-router-dom";
import { updateListAC, myAxios } from "../../redux/reducers/reducer_ListTask";
import { connect } from "react-redux";

function NavBar(props) {
    return (
        <div className="btn-group" role="group" aria-label="Basic example">
            <NavLink to='/AddTask' className="btn btn-primary"> </NavLink>
            <NavLink to='/listTask' className="btn btn-primary"> </NavLink>
            <button className="btn btn-primary" onClick={props.mydispatch}> </button>
        </div>
    )
}

let mapStateToProps = (state) => {
    return {}
}
let mapDispatchToProps = (dispatch) => {
    return {
        mydispatch: function (e) {
            myAxios().then(function (response) {
                    dispatch(
                        updateListAC(JSON.parse(response.data))                        
                    )
                })
        }
    }
}


let UpdateListContainer = connect(mapStateToProps, mapDispatchToProps)(NavBar);

export default UpdateListContainer;