import React, { Component } from 'react';
import { NavLink } from "react-router-dom";

class ListTask extends Component {
    componentDidMount() {
        this.props.getListTask()
    }

    ChangeDataFilter = (e) => {
        this.props.ChangeDataFilterAC({
            name: e.target.getAttribute('data-name'),
            data: e.target.value
        })
    }
    getListTask = (e) => {
        if (e.key == 'Enter') {
            this.props.getListTask(this.props.filterListTask)
        }
    }
    rowFilter = (name) => <th scope="col" className='align-top'>
        <input type='text' className='w-100'
            onKeyPress={this.getListTask}
            onChange={this.ChangeDataFilter}
            data-name={name} value={this.props.filterListTask[{name}]} />
    </th>
    render() {
        //console.log(this.props)

        let rows = this.props.listTasks.map(item => <tr key={item.idtask}>
            <th scope="row"><NavLink to={'/react/AddTaskDO/step1/'+item.idtask} onClick={this.props.resetErrorsAC}>{item.idtask}</NavLink></th>
            <td>{item.dateTask}</td>
            <td>{item.authorTask}</td>
            <td>{item.statusTask}</td>
            <td>{item.datelastiteration}</td>
            <td>{item.idpk}</td>
            <td>{item.managerDO}</td>
            <td>{item.department}</td>
            <td>{item.typedoc}</td>
            <td>{item.GID}</td>
            <td>{item.nameClient}</td>
            <td>{item.subjectTask}</td>
            <td>{item.comment}</td>
        </tr>)


        //console.log(rows)
        return (
            <div>
                <table className="table table-sm">
                    <thead>
                        <tr>
                            <th scope="col" className='align-top'>ID </th>
                            <th scope="col" className='align-top'> </th>
                            <th scope="col" className='align-top'> </th>
                            <th scope="col" className='align-top'> </th>
                            <th scope="col" className='align-top'>  <br />( /)</th>
                            <th scope="col" className='align-top'>id </th>
                            <th scope="col" className='align-top'> </th>
                            <th scope="col" className='align-top'></th>
                            <th scope="col" className='align-top'> </th>
                            <th scope="col" className='align-top'>GID</th>
                            <th scope="col" className='align-top'>  </th>
                            <th scope="col" className='align-top'> </th>
                            <th scope="col" className='align-top'> </th>
                        </tr>
                        <tr>
                            {this.rowFilter('idtask')}
                            {this.rowFilter('dateTask')}
                            {this.rowFilter('authorTask')}
                            {this.rowFilter('statusTask')}
                            {this.rowFilter('datelastiteration')}
                            {this.rowFilter('idpk')}
                            {this.rowFilter('managerDO')}
                            {this.rowFilter('department')}
                            {this.rowFilter('typedoc')}
                            {this.rowFilter('GID')}
                            {this.rowFilter('nameClient')}
                            {this.rowFilter('subjectTask')}
                            {this.rowFilter('comment')}
                        </tr>
                    </thead>
                    <tbody>
                        {rows}
                    </tbody>
                </table>
            </div>
        )
    }

}

export default ListTask