import { applyMiddleware, combineReducers, createStore } from "redux";
import listTaskReducer from "./reducers/reducer_ListTask";
import directoryesReducer from "./reducers/reducer_directorys";
import TaskDOReducer from "./reducers/reducer_taskDO";
import thunkMiddleware from "redux-thunk";
import { reducer as formReducer } from 'redux-form'

let reducers = combineReducers({
    arrTasks: listTaskReducer,
    directoryes: directoryesReducer,
    TaskDOForm: TaskDOReducer,
    form: formReducer
});

let store = createStore(reducers,applyMiddleware(thunkMiddleware));

export default store;