function AddTask(props) {
  //debugger;
  let executors=props.dirExecutors?props.dirExecutors.map(item=><option kay={item.id} value={item.id}>{item.name}</option>):[];
  let tyteTasks=props.dirTyteTasks?props.dirTyteTasks.map(item=><option kay={item.id} value={item.id}>{item.name}</option>):[];
  return (
    <div>
      <form onSubmit={props.addElem}>
        <h5 className='d-inline-block'> </h5>
        <div className="mb-3">
          <label className="form-label"> </label>
          <select className="form-control" value={props.dataTask.executor_id} onChange={props.callTmpElem} data-name='executor_id' data-id='0'>
            <option></option>
            {executors}
          </select>
        </div>
        <div className="row">
          <div className="col-6">
            <div className="mb-3">
              <label className="form-label"> </label>
              <textarea className="form-control" rows="3"
                onChange={props.callTmpElem} data-name='task' data-id='0' value={props.dataTask.task}></textarea>
            </div>
          </div>
          <div className="col-6">
            <div className="mb-3">
              <label className="form-label"> </label>
              <textarea className="form-control" rows="3"
                onChange={props.callTmpElem} data-name='result' data-id='0' value={props.dataTask.result}></textarea>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-4">
            <label className="form-label">  </label>
            <input type="date" className="form-control"
              onChange={props.callTmpElem} data-name='dateBegeen' data-id='0'
              value={props.dataTask.dateBegeen}></input>
          </div>
          <div className="col-4">
            <label className="form-label"> </label>
            <input type="date" className="form-control"
              onChange={props.callTmpElem} data-name='dateEnd' data-id='0'
              value={props.dataTask.dateEnd}></input>
          </div>
          <div className="col-4">
            <label className="form-label"> </label>
            <select className="form-control"
              onChange={props.callTmpElem} data-name='tyteTask_id' data-id='0'
              value={props.dataTask.tyteTask_id}>
              <option></option>
              {tyteTasks}
            </select>
          </div>
        </div>

        <button type="submit" className="btn btn-primary w-100 mt-2"></button>
      </form>

    </ div>
  )
}
export default AddTask;