import Step2 from "./step2";
import { connect } from "react-redux";
import { withRouter } from "react-router";
import { compose } from "redux";
import { changeDataAC, setSelectedClientDataAC, getClients,
    toggleAddClientFormAC,getContracts,getTask,getListservicesByGid,touchedAC  } from "../../../redux/reducers/reducer_taskDO";

let mapStateToProps = (state) => {
    //debugger;
    return {
        selectContractFormToggle: state.TaskDOForm.toggleForms.selectContractForm,
        addClientFormToggle: state.TaskDOForm.toggleForms.addClientForm,
        MainCP: state.TaskDOForm.temporaryTaskDO.MainCP,
        TypeService: state.TaskDOForm.temporaryTaskDO.TypeService,
        ClientListSelected: state.TaskDOForm.ClientListSelected,
        clientsList: state.TaskDOForm.clientsList,
        errors: state.TaskDOForm.errors,
        currentTeask: state.TaskDOForm.currentTeask,
        statusUser: state.TaskDOForm.statusUser,
        temporaryTaskDO: state.TaskDOForm.temporaryTaskDO
    }
}

let Step2Container = compose(
    connect(mapStateToProps, 
        {changeDataAC, setSelectedClientDataAC, getClients,toggleAddClientFormAC,getContracts,getTask,getListservicesByGid,touchedAC }),
    withRouter
)(Step2)

export default Step2Container
