import SelectContract from "./selectContract";
import { connect } from "react-redux";
import { setPurentDocumentAC,getContracts } from "../../../redux/reducers/reducer_taskDO";

let mapStateToProps = (state) => {
    //debugger;
    return {
        num_doc_parent: state.TaskDOForm.temporaryTaskDO.num_doc_parent,
        contracts: state.TaskDOForm.contracts,
        TypeService: state.TaskDOForm.temporaryTaskDO.TypeService,
        MainCP: state.TaskDOForm.temporaryTaskDO.MainCP
    }
}

let SelectContractContainer = connect(mapStateToProps, 
    {setPurentDocumentAC,getContracts})(SelectContract);
export default SelectContractContainer
