import logo from '../logo.svg';
import '../App.css';
import NavBar from "./navTask/navTask";
import { Route } from 'react-router-dom';
import ContainerUpdateTask from "./form/updateTask/containerUpdateTask";
import ContainerAddTask from "./form/addTask/containerAddTask";
import ContainerAlerts from "./tasksAlert/containerAlerts";
import ContainerListTasks from "./listTasks/containerListTasks";

function TodoStart() {
    return (
        <div>
            <NavBar />
            <img src={logo} className="App-logo float-right mb-2" alt="logo" />
            <Route path="/AddTask" render={() => <ContainerAddTask />} />
            <ContainerUpdateTask />
            <Route exact path="/AddTaskInside/" render={() => <ContainerAlerts />} />
            <Route path="/AddTaskInside/listTask" render={() => <ContainerAlerts />} />
            <Route path="/AddTaskInside/listTask" render={() => <ContainerListTasks />} />
            <Route path="/AddTaskDO" render={() => <div>hello</div>} />
        </div>
    )
}

export default TodoStart