import { connect } from "react-redux";
import TasksAlert from "./tasksAlert";

let mapStateToProps = (state) => {
  return {
    dataTasks: state.arrTasks.arrTasks,
    dateDiff: function (date, toDay, status) {
      //debugger;
      //console.log(status);
      toDay = new Date(toDay);
      date = new Date(date);
      return toDay > date && status == null;
    },
    dateEqual: function (date, toDay) {
      return date == toDay;
    }
  }
}
let mapDispatchToProps = (dispatch) => {
  return {}
}

let AlertsContainer = connect(mapStateToProps, mapDispatchToProps)(Alerts);

function Alerts(props) {
  return (
    <div>
      <TasksAlert dataTasks={props.dataTasks} difDate={props.dateDiff} borderColor="border-danger" totalText=" " />
      <TasksAlert dataTasks={props.dataTasks} difDate={props.dateEqual} borderColor="border-warning" totalText="  " />
    </div>
  )
} 

export default AlertsContainer