import { NavLink } from "react-router-dom";

/* import { updateListAC, myAxios } from "../../redux/reducers/reducer_ListTask";
import { connect } from "react-redux"; */

function NavBar(props) {
    //debugger;
    //console.log(props)
    var Myerror = {};

    var check = (item, number) => {
        if(!(props.errors.allInputs.findIndex(itemF => itemF == item) == -1)){
            return props.errors[item].step == number && props.errors[item].error && props.errors[item].touched
        }
    }

    //debugger
    //props.errors[item].step==2&&props.errors[item].error&&props.errors[item].touched
    Object.keys(props.errors).forEach(item => {
        /* if (item == 'MainCP') {
            console.log(item)
            console.log(props.errors[item].touched)
        } */

        /* console.log(item)
        console.log(item.touched) */
        /* console.log(props.errors['item']) */
        if (check(item, 1)) Myerror.step1 = true;
        if (check(item, 2)) Myerror.step2 = true;
        if (check(item, 3)) Myerror.step3 = true;
        if (check(item, 4)) Myerror.step4 = true;
        if (check(item, 5)) Myerror.step5 = true;
    })
    let idTask = props.match.params.idtask ? `/${props.match.params.idtask}` : '';
    return (
        <div className="btn-group" role="group" aria-label="Basic example">
            <NavLink to={`/react/AddTaskDO/step1${idTask}`} className={Myerror.step1 ? 'btn btn-danger' : 'btn btn-primary'}> 1</NavLink>
            <NavLink to={`/react/AddTaskDO/step2${idTask}`} className={Myerror.step2 ? 'btn btn-danger' : 'btn btn-primary'}> 2</NavLink>
            <NavLink to={`/react/AddTaskDO/step3${idTask}`} className={Myerror.step3 ? 'btn btn-danger' : 'btn btn-primary'}> 3</NavLink>
            <NavLink to={`/react/AddTaskDO/step4${idTask}`} className={Myerror.step4 ? 'btn btn-danger' : 'btn btn-primary'}> 4</NavLink>
            <NavLink to={`/react/AddTaskDO/step5${idTask}`} className={Myerror.step5 ? 'btn btn-danger' : 'btn btn-primary'}> 5</NavLink>
            <NavLink to={`/react/AddTaskDO/allSteps${idTask}`} className="btn btn-primary"> </NavLink>
            <NavLink to='/react/AddTaskDO/listTask' className="btn btn-primary"> </NavLink>
        </div>
    )
}

export default NavBar;