import Step4 from "./step4";
import { connect } from "react-redux";
import { withRouter } from "react-router";
import { compose } from "redux";
import { getSY, changeDataAC, clearDataAC,getTask,touchedAC } from "../../../redux/reducers/reducer_taskDO";


let mapStateToProps = (state) => {
    //debugger;
    return {
        temporaryTaskDO: state.TaskDOForm.temporaryTaskDO,
        refSY: state.TaskDOForm.refSY,
        errors: state.TaskDOForm.errors,
        currentTeask: state.TaskDOForm.currentTeask
    }
}

let Step4Container = compose(connect(mapStateToProps,
    { getSY, changeDataAC, clearDataAC,getTask,touchedAC }),
    withRouter
)(Step4);
export default Step4Container