import * as axios from "axios"

const ADDTASK = 'AddTask';
const UPDATETASK = 'UdateTask';
const TEMPORARYTASK = 'temporaryTask';
const CHANGESTATUS = 'ChangeStatus';
const CHANGEIDTASK = 'changeIdTask';
const UPDATELIST = 'updateList';
const SAVEITEM = 'saveItem';


export function myAxios(method='get',data={}) {
    return (
        axios({
            method: method,
            url: 'http://localhost',
            data: data
        })
    )
}

let initialState = {
    temporaryTask:
    {
        id: null,
        dateCreate: '',
        dateBegeen: '',
        dateEnd: '',
        executor_id: 0,
        executor_name: '',
        task: '',
        result: '',
        tyteTask_id: 0,
        tyteTask_name: '',
        dateComplete: '',
        textComplete: '',
        statusTask: ''
    }
};
export const saveItemAC = (data) => {
    return {
        type: SAVEITEM,
        id: data.id
    };
}
export const updateListAC = (data) => {
    return {
        type: UPDATELIST,
        data: data
    };
}
export const addTaskAC = () => {
    return {
        type: ADDTASK
    };
}
export const UpdateTaskAC = (date, name, id) => {
    return {
        type: UPDATETASK,
        data: date,
        name: name,
        id: id
    };
}
export function changeStatusAC(data) {
    return {
        type: CHANGESTATUS,
        data: data.data,
        name: data.name,
        id: data.id
    }
}
export function TmpElemAC(data) {
    return (
        {
            type: TEMPORARYTASK,
            data: data.data,
            name: data.name,
            id: data.id
        }
    )
}
export function changeIdTask(id) {
    return (
        {
            type: CHANGEIDTASK,
            changeIdTask: id
        }
    )
}

function listTaskReducer(state = initialState, action) {
    //debugger;
    switch (action.type) {
        case 'ApdateOneItemFromDB':
            var newState = {
                ...state,
                arrTasks: [...state.arrTasks]
            };
            var taskIndex = newState.arrTasks.findIndex(item => item.id == action.data[0].id);
            newState.arrTasks[taskIndex] = { ...action.data[0] };
            return newState;
        case SAVEITEM:
            var task = state.arrTasks.find(item => item.id == action.id);
            var jsonTask = JSON.stringify(task)
            myAxios('post',{updateTask: jsonTask}).then(function (response) {
                    var newData = JSON.parse(response.data);
                    action.dispatch({ type: 'ApdateOneItemFromDB', data: newData })
                });
            return state
        case UPDATELIST:
            //alert('helloKPP')
            var newState = {
                ...state,
                arrTasks: [...action.data]
            };
            //var newState= action.data

            return newState
        case 'addNewTask':
            var newState = {
                ...state,
                arrTasks: [...state.arrTasks, { ...action.data[0] }]
            };
            return newState;

        case ADDTASK:
            var newState = {
                ...state,
                arrTasks: [...state.arrTasks]
            };
            let cloneAddTask = Object.assign({}, state.temporaryTask);
            var now = new Date();
            var m = now.toLocaleString("ru", { month: '2-digit' });
            var d = now.toLocaleString("ru", { day: '2-digit' });
            var y = now.toLocaleString("ru", { year: 'numeric' });

            cloneAddTask.id = state.arrTasks.length;
            cloneAddTask.dateCreate = `${y}-${m}-${d}`;

            var jsonTask = JSON.stringify(cloneAddTask)
            myAxios('post',{addTask: jsonTask}).then(function (response) {
                    var newData = JSON.parse(response.data);
                    action.dispatch({ type: 'addNewTask', data: newData })
                });
            return state

            newState.arrTasks.push(cloneAddTask);
            return newState;
        case UPDATETASK:
            var newState = {
                ...state,
                arrTasks: [...state.arrTasks]
            };
            var idTask = newState.arrTasks.findIndex(item => item.id == action.id);
            newState.arrTasks[idTask][action.name] = action.data;
            switch (action.name) {
                case "executor_id":
                    switch (action.data) {
                        case '1':
                            newState['arrTasks'][idTask]['executor_name'] = ' .';
                            break;
                        case '2':
                            newState['arrTasks'][idTask]['executor_name'] = ' .';
                            break;
                        case '3':
                            newState['arrTasks'][idTask]['executor_name'] = ' .';
                            break;
                    }
                case "tyteTask_id":
                    switch (action.data) {
                        case '1':
                            newState['arrTasks'][idTask]['tyteTask_name'] = '';
                            break;
                        case '2':
                            newState['arrTasks'][idTask]['tyteTask_name'] = '';
                            break;
                        case '3':
                            newState['arrTasks'][idTask]['tyteTask_name'] = '';
                            break;
                    }
            }
            newState.arrTasks[idTask].saved = 0;
            return newState;
        case TEMPORARYTASK:
            var tmpState = {
                ...state,
                temporaryTask: { ...state.temporaryTask }
            };
            tmpState.temporaryTask[action.name] = action.data;
            switch (action.name) {
                case "executor_id":
                    switch (action.data) {
                        case '1':
                            tmpState['temporaryTask']['executor_name'] = ' .';
                            break;
                        case '2':
                            tmpState['temporaryTask']['executor_name'] = ' .';
                            break;
                        case '3':
                            tmpState['temporaryTask']['executor_name'] = ' .';
                            break;
                    }
                case "tyteTask_id":
                    switch (action.data) {
                        case '1':
                            tmpState['temporaryTask']['tyteTask_name'] = '';
                            break;
                        case '2':
                            tmpState['temporaryTask']['tyteTask_name'] = '';
                            break;
                        case '3':
                            tmpState['temporaryTask']['tyteTask_name'] = '';
                            break;
                    }
            }
            return tmpState;
        case CHANGESTATUS:

            var newState = {
                ...state,
                arrTasks: [...state.arrTasks]
            };
            var idTask = newState.arrTasks.findIndex(item => item.id == action.id);
            var now = new Date();
            var m = now.toLocaleString("ru", { month: '2-digit' });
            var d = now.toLocaleString("ru", { day: '2-digit' });
            var y = now.toLocaleString("ru", { year: 'numeric' });

            newState.arrTasks[idTask][action.name] = action.data;

            newState.arrTasks[idTask]['dateComplete'] = `${y}-${m}-${d}`;
            newState.arrTasks[idTask].saved = 0;
            return newState;
        default:
            return state;
    }

}
export default listTaskReducer;