import Step1 from "./step1";
import { connect } from "react-redux";
import { withRouter } from "react-router";
import { compose } from "redux";
import {
    toggleSelectContractFormAC, changeDataAC, rulesStep1AC,
    getTypesContract, getDepartments, clearRefSYAC, getTask,getuserdata,touchedAC
} from "../../../redux/reducers/reducer_taskDO";

let mapStateToProps = (state) => {
    //debugger;
    return {
        selectContractFormToggle: state.TaskDOForm.toggleForms.selectContractForm,
        OneOrVileTasks: state.TaskDOForm.temporaryTaskDO.OneOrVileTasks,
        TypeDoc: state.TaskDOForm.temporaryTaskDO.TypeDoc,
        TemplateDoc: state.TaskDOForm.temporaryTaskDO.TemplateDoc,
        TypeService: state.TaskDOForm.temporaryTaskDO.TypeService,
        Dep: state.TaskDOForm.temporaryTaskDO.Dep,
        temporaryTaskDO: state.TaskDOForm.temporaryTaskDO,
        typeContractsList: state.TaskDOForm.typeContractsList,
        departmentsList: state.TaskDOForm.departmentsList,
        errors: state.TaskDOForm.errors,
        currentTeask: state.TaskDOForm.currentTeask

    }
}

let Step1Container = compose(
    connect(mapStateToProps,
        { toggleSelectContractFormAC, changeDataAC, rulesStep1AC, 
            getTypesContract, getDepartments, clearRefSYAC, getTask,getuserdata,touchedAC }),
    withRouter
)(Step1);

export default Step1Container