import AdminForm from "./adminForm";
import { MyChekBoxKPP, MyTextAreaKPP } from "../inputs/inputs";

function Step5(props) {
    //debugger
    switch (props.statusUser) {
        case 'admin':
            var btnGroup = <AdminForm />
            break;
        case 'managerDO':
            break;
        case 'manaregPP':
            var btnGroup = <button className='btn btn-primary w-100' onClick={()=>{props.checkErrorsAC();props.sendDataAC()}}> </button>
            break;
    }
    function changeData(e) {
        props.changeDataAC({
            name: e.target.getAttribute('data-name'),
            data: e.target.value
        })
    }
    function validationRequired(name) {
        //debugger
        var find = !(props.errors.allInputs.findIndex(item => item == name) == -1)
        return find && props.errors[name].touched && props.errors[name].error
    }
    function touched(name) {
        //debugger
        return () => props.touchedAC(name)
    }
    //console.log(props);
    let datdPropsChekBox = {
        onBlur: touched,
        validation: validationRequired,
        values: [
            { id: 1, name: '' },
            { id: 0, name: '' }
        ],
        onChange: changeData
    }
    let datdPropsTextArea = {
        rows: '3',
        onChange: changeData,
        onBlur: touched,
        validation: validationRequired,
    }
    return (
        <div>
            <h5>.5 </h5>
            <hr />
            <MyTextAreaKPP {...props} name='signerClient'
                title='   '
                {...datdPropsTextArea} />
            <MyTextAreaKPP {...props} name='ContactManagerGA'
                title='   '
                {...datdPropsTextArea} />
            <MyTextAreaKPP {...props} name='ContactsCp'
                title='   '
                {...datdPropsTextArea} />
            <MyTextAreaKPP {...props} name='systemsClient'
                title='        '
                {...datdPropsTextArea} />
            <MyTextAreaKPP {...props} name='CommentCD'
                title=' '
                {...datdPropsTextArea} />
            <MyChekBoxKPP {...props}
                title=' '
                name='PDNBool'
                {...datdPropsChekBox} />
            <MyChekBoxKPP {...props}
                title='  '
                name='boxServiceBool'
                {...datdPropsChekBox} />
            <MyChekBoxKPP {...props}
                title='   '
                name='coExecutorBool'
                {...datdPropsChekBox} />
            <MyChekBoxKPP {...props}
                title='    '
                name='RIDBool'
                {...datdPropsChekBox} />
            <MyChekBoxKPP {...props}
                title='     '
                name='startInPastBool'
                {...datdPropsChekBox} />
            <MyChekBoxKPP {...props}
                title='    '
                name='worcClientAreaBool'
                {...datdPropsChekBox} />
            <MyChekBoxKPP {...props}
                title='     '
                name='TransferAreaBool'
                {...datdPropsChekBox} />
            <MyChekBoxKPP {...props}
                title='     '
                name='TransferToolsBool'
                {...datdPropsChekBox} />
            {btnGroup}
        </div>
    )

}
export default Step5
