import React, { Component } from 'react';

class SelectContract extends Component {
    selectContract = (e) => {
        e.preventDefault();
        //alert(e.target.firstChild.data.trim())
        this.props.setPurentDocumentAC({
            id: e.target.getAttribute('data-id'),
            number: e.target.firstChild.data.trim()
        })
    }
    componentDidMount() {
        this.props.getContracts(this.props.MainCP[0],this.props.TypeService)
    }

    render() {
        //console.log(this.props)
        let rows = this.props.contracts.map(item =>
            <tr key={item.id}>
                <th scope="row">
                    <a href='#' data-id={item.id} onClick={this.selectContract}>{item.numberContract}</a>
                </th>
                <td>{item.dateContract}</td>
                <td>{item.subject}</td>
            </tr>)
        
        return (
            <div>
                <div className="form-group">
                    <label><h6> :</h6></label>
                    <input
                        type="text" className="form-control" data-idparent={this.props.num_doc_parent.id}
                        value={this.props.num_doc_parent.number}
                        readOnly={true} />
                </div>
                <div>
                    <label><h6>  ( ):</h6></label>
                    <table className="table table-sm">
                        <thead>
                            <tr>
                                <th scope="col"> </th>
                                <th scope="col"> </th>
                                <th scope="col"> </th>
                            </tr>
                        </thead>
                        <tbody>
                            {rows}
                        </tbody>
                    </table>
                </div>
            </div>
        )
    }

}

export default SelectContract