import React from 'react'
import { MyInputFileKPP } from "../inputs/inputs";

class Step4 extends React.Component {
    constructor(props) {
        super(props);
        this.changeData = this.changeData.bind(this)
        this.clearData = this.clearData.bind(this)
    }
    componentDidMount() {
        if (this.props.temporaryTaskDO.TypeService) this.props.getSY(this.props.temporaryTaskDO.TypeService);
        let urlIdTask = this.props.match.params.idtask;
        if (urlIdTask &&
            urlIdTask != this.props.currentTeask) this.props.getTask(urlIdTask)
    }
    clearData(e) {
        e.preventDefault();
        this.props.clearDataAC({ name: 'FileSU' })
    }
    selectedFiles(arr) {
        if (!arr.length) return <></>
        var newArr = [];
        for (var i = 0; i < arr.length; i++) {
            newArr.push(arr[i])
            newArr[i].id = i
        }
        //console.log(newArr)
        let selectedFilesSY = newArr.map(item =>
            <p className='ml-2' key={item.id}>{item.name}</p>)
        {/* <span className='finger'
                data-id={item.id}
                onClick={this.deleteServicesListSelected}>&#10060;</span> */}
        return <div>
            <h6 className='d-inline-block'> </h6>
            <a className='ml-3' onClick={this.clearData} href='#'>&#10060;</a>
            {selectedFilesSY}
        </div>
    }
    changeData(e) {
        /* const file = event.target.files[0];
        let formData = new FormData();
        formData.append('file', file);
        //console.log(formData)
        //Make a request to server and send formData */
        //debugger;
        this.props.changeDataAC({
            name: e.target.getAttribute('data-name'),
            data: e.target.files
        })
    }
    validationRequired = (name) => {
        //debugger
        var find = !(this.props.errors.allInputs.findIndex(item => item == name) == -1)
        //console.log(name+': '+find)
        if(name=='FileSU'){
            console.log(name+' touched: '+this.props.errors[name].touched)
            console.log(name+' error: '+this.props.errors[name].error)
        }
        
        return find && this.props.errors[name].touched && this.props.errors[name].error
        
    }
    touched = (name) => {
        //debugger
        return () => this.props.touchedAC(name)
    }

    render() {
        if (this.props.refSY[0].ref) {
            var ref = (<div className="form-group">
                <label><h6> :</h6></label>
                <a href={this.props.refSY[0].ref} className='btn btn-light d-block text-left' download>{this.props.refSY[0].name}</a>
            </div>)
        }
        return (
            <div>
                <h5>.4 </h5>
                <hr />
                {ref}
                <MyInputFileKPP {...this.props} title='  :' name='FileSU'
                onChange={this.changeData}
                onBlur={this.touched}
                validation={this.validationRequired} />
                <MyInputFileKPP {...this.props} title='  :' name='FilePrice'
                onChange={this.changeData}
                onBlur={this.touched}
                validation={this.validationRequired} />
                <MyInputFileKPP {...this.props} title='     :' name='FileEdit'
                onChange={this.changeData}
                onBlur={this.touched}
                validation={this.validationRequired} />
                <MyInputFileKPP {...this.props} title='   :' name='FileApplication'
                onChange={this.changeData}
                onBlur={this.touched}
                validation={this.validationRequired} />
                <MyInputFileKPP {...this.props} title='  :' name='FileUS'
                onChange={this.changeData}
                onBlur={this.touched}
                validation={this.validationRequired} />
                <MyInputFileKPP {...this.props} title='  :' name='FileOther'
                onChange={this.changeData}
                onBlur={this.touched}
                validation={this.validationRequired} />
            </div>
        )
    }
}
export default Step4