import AddClient from "./addClient";
import { connect } from "react-redux";
import { changeDataNewClientAC,saveNewClient } from "../../../redux/reducers/reducer_taskDO";

let mapStateToProps = (state) => {
    //debugger;
    return {
        addClient: state.TaskDOForm.addClient
    }
}


let AddClientContainer = withChangeDataNewClient(connect(mapStateToProps,{saveNewClient})(AddClient));
export default AddClientContainer

function withChangeDataNewClient(component){
    let changeData= (e)=>{
        //debugger;
        return changeDataNewClientAC({
            name: e.target.getAttribute('data-name'),
            data: e.target.value
        })
    }
    let withChangeDataNewClientMapStateToProps = (state) => {
        return {}
    }
    
    return connect(withChangeDataNewClientMapStateToProps,{changeData})(component);
}
