import React from "react"
import { MySelectKPP, MyInputKPP, MyChekBoxKPP } from "../inputs/inputs";




class Step1 extends React.Component {
    constructor(props) {
        super(props);
        this.toggleSelectContractForm = this.toggleSelectContractForm.bind(this)
        this.changeData = this.changeData.bind(this)
        this.getDepartments = this.getDepartments.bind(this)
    }

    componentDidMount() {
        this.props.getTypesContract()
        if (this.props.TypeService) this.props.getDepartments(this.props.TypeService)
        let urlIdTask = this.props.match.params.idtask;
        if (urlIdTask &&
            urlIdTask != this.props.temporaryTaskDO.ID) this.props.getTask(urlIdTask)
    }

    getDepartments(e) {
        this.props.getDepartments(e.target.value, true)
    }

    toggleSelectContractForm(e) {
        var datatoggle = e.target.getAttribute('data-bool');
        datatoggle = datatoggle == 'true' ? true : false;
        this.props.toggleSelectContractFormAC(datatoggle)
    }
    changeData(e) {
        //debugger;
        //console.log(e.target.getAttribute('value'))
        this.props.changeDataAC({
            name: e.target.getAttribute('data-name'),
            data: e.target.value
        })
    }
    touched = (name) => {
        //debugger
        return () => this.props.touchedAC(name)
    }

    validationRequired = (name) => {
        var find = !(this.props.errors.allInputs.findIndex(item => item == name) == -1)
        return find && this.props.errors[name].touched && this.props.errors[name].error
    }

    render() {
        let checked = {
            OneOrVileTasks_one: this.props.OneOrVileTasks == 'one' ? true : false,
            OneOrVileTasks_vile: this.props.OneOrVileTasks == 'vile' ? true : false,
            TypeDoc_contract: this.props.TypeDoc == 1 ? true : false,
            TypeDoc_DS: this.props.TypeDoc == 2 ? true : false

        }

        let typesContract = this.props.typeContractsList.map(item => <option key={item.id} value={item.id}>{item.name}</option>)
        let departmentsList = this.props.departmentsList.map(item => <option key={item.id} value={item.id}>{item.name}</option>)
        //console.log(this.props.temporaryTaskDO.TypeService)
        return (
            <div>
                <h5>.1 </h5>
                <hr />
                <MyChekBoxKPP {...this.props} name='TypeDoc' title=' '
                    onBlur={this.touched}
                    validation={this.validationRequired}
                    values={[
                        { id: 1, name: '' },
                        { id: 2, name: '' }
                    ]}
                    onChange={(e) => { this.toggleSelectContractForm(e); this.changeData(e); this.props.rulesStep1AC(); }}
                />
                <MyInputKPP {...this.props} onChange={this.changeData}
                    onBlur={this.touched}
                    validation={this.validationRequired}
                    name='TemplateDoc' title='   ( )' />
                <MySelectKPP {...this.props} size='10' name='TypeService' title=' '
                    onChange={(e) => { this.changeData(e); this.getDepartments(e); this.props.clearRefSYAC() }}
                    onBlur={this.touched}
                    validation={this.validationRequired}>
                    {typesContract}
                </ MySelectKPP>
                <MySelectKPP {...this.props} name='Dep' title='  '
                    onChange={this.changeData}
                    onBlur={this.touched}
                    validation={this.validationRequired}>
                    {departmentsList}
                </ MySelectKPP>
            </div>
        )

    }

}
export default Step1