import { connect } from "react-redux";
import ListTasks from "./listTasks";
import { myAxios } from "../../redux/reducers/reducer_ListTask";

let mapStateToProps = (state) => {
    return {
        dataTasks: state.arrTasks.arrTasks,
        title: " "
    }
}
let mapDispatchToProps = (dispatch) => {
    //debugger;
    return {
        updateList: function (e) {
            myAxios().then(function (response) {
                dispatch({
                    type: 'updateList',
                    data: JSON.parse(response.data)
                })
            })
        },
        updateDirs: function () {
            myAxios('post', { updateDirs: 1 }).then(function (response) {
                //console.log(JSON.parse(response.data));
                dispatch({
                    type: 'updateDirs',
                    data: JSON.parse(response.data)
                })
            })

        }
    }
}
function borderListTasks(props) {
    return (
        <div className={"alert mt-2 bg-transparent border-2 border-secondary"}>
            <ListTasks
                dataTasks={props.dataTasks}
                title={props.title}
                updateList={props.updateList}
                updateDirs={props.updateDirs}
            />
        </div>
    )
}

let ContainerListTasks = connect(mapStateToProps, mapDispatchToProps)(borderListTasks);

export default ContainerListTasks;