import ListTask from "./listTask";
import { connect } from "react-redux";
import { getListTask,ChangeDataFilterAC,resetErrorsAC } from "../../../redux/reducers/reducer_taskDO";

let mapStateToProps = (state) => {
    //debugger;
    return {
        listTasks: state.TaskDOForm.listTasks,
        filterListTask: state.TaskDOForm.filterListTask
    }
}

let ListTaskContainer = connect(mapStateToProps, 
    {getListTask,ChangeDataFilterAC,resetErrorsAC})(ListTask);
export default ListTaskContainer