import AddService from "./addService";
import { connect } from "react-redux";
import { changeDataNewServiceAC, saveNewService,getTypesContract,getDepartments } from "../../../redux/reducers/reducer_taskDO";
import { compose } from "redux";

function withChangeDataNewService(component){
    let changeData= (e)=>{
        //debugger;
        return changeDataNewServiceAC({
            name: e.target.getAttribute('data-name'),
            data: e.target.value
        })
    }
    let withChangeDataNewClientMapStateToProps = (state) => {
        return {}
    }
    
    return connect(withChangeDataNewClientMapStateToProps,{changeData})(component);
}

let mapStateToProps = (state) => {
    return {
        addService: state.TaskDOForm.addService,
        typeContractsList: state.TaskDOForm.typeContractsList,
        departmentsList: state.TaskDOForm.departmentsList
    }
}

export default compose(
    connect(mapStateToProps,{saveNewService,getTypesContract,getDepartments}), 
    withChangeDataNewService
)(AddService)