import { connect } from "react-redux";
import AddTask from "./addTask";
import { addTaskAC, TmpElemAC } from "../../../redux/reducers/reducer_ListTask";

let mapStateToProps = (state) => {
    //debugger;
    return {
        dataTask: state.arrTasks.temporaryTask,
        dirExecutors: state.directoryes.dir_executors,
        dirTyteTasks: state.directoryes.dir_tyteTasks
    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        callTmpElem: function(e) {
            dispatch(
                TmpElemAC({
                    data: e.target.value,
                    name: e.target.getAttribute('data-name'),
                    id: e.target.getAttribute('data-id')
                })
            )
        },
        addElem: function(e) {
            e.preventDefault();
            var action=addTaskAC();
            action.dispatch=dispatch;
            dispatch(
                action
            )
          }

    }
}


let AddTaskContainer = connect(mapStateToProps, mapDispatchToProps)(AddTask);

export default AddTaskContainer;