import { MyInput, MyTextArea, MySelect } from "../inputs/inputs";
import { Form, Field } from 'react-final-form'

function AddClientForm(props) {
    //debugger;

    function saveNewClient() {
        props.saveNewClient(props.addClient)
    }

    const onSubmit = values => {
        props.saveNewClient(values)
    }
    console.log(props);
    return (
        <Form
            onSubmit={onSubmit}
            validate={values => {
                let requiredText = '   .';
                const errors = {}
                //console.log(Object.keys(values));
                let requireditems = [
                    "inn",
                    "gid",
                    "name",
                    "contects"
                ]
                requireditems.forEach(item => { if (!values[item]) errors[item] = requiredText })
                return errors
            }}
            initialValues={props.addClient}
            render={({ handleSubmit, form, submitting, pristine, values }) => (
                <form onSubmit={handleSubmit}>
                    <div className='alert alert-light'>
                        <div className="form-group">
                            <label><h6 className='text-body'>:</h6></label>
                            <Field type='number' name='inn' component={MyInput} />
                        </div>
                        <div className="form-group">
                            <label><h6 className='text-body'>GID:</h6></label>
                            <Field type='number' name='gid' component={MyInput} />
                        </div>
                        <div className="form-group">
                            <label><h6 className='text-body'> :</h6></label>
                            <Field rows='3' name='name' component={MyTextArea} />
                        </div>
                        <div className="form-group">
                            <label><h6 className='text-body'>    :</h6></label>
                            <Field rows='3' name='contects' component={MyTextArea} />
                        </div>
                        <div className="form-group">
                            <label><h6> :</h6></label>
                            <a href="#" className='btn btn-light d-block text-left' download> </a>
                        </div>
                        <button type='submit' className='btn btn-primary w-100'></button>
                    </div>
                </form>
            )}
        />)
}
export default AddClientForm