import ListTasks from "../listTasks/listTasks";
function TasksAlert(props) {
    //debugger;
    var now=new Date();
    var m=now.toLocaleString("ru", {month: '2-digit'});
    var d=now.toLocaleString("ru", {day: '2-digit'});
    var y=now.toLocaleString("ru", {year: 'numeric'});
    var toDay=`${y}-${m}-${d}`;

    let arrToDay=[];

    if(!props.dataTasks){
        return <div></div>
    }

    props.dataTasks.forEach(function(item) {
        if(props.difDate(item.dateEnd, toDay,item.statusTask)){
            arrToDay.push(item);
        }
      });
    if(!arrToDay.length) return "";
    return (
        <div className={"alert mt-2 bg-transparent border-2 "+props.borderColor}>
            <ListTasks dataTasks={arrToDay} title={props.totalText}/>
        </div>
    )
}

export default TasksAlert