import SelectContractContainer from "../changs/selectContractContainer";
import AddClientContainer from "../changs/addClientContainer";
import React from "react"
import { MySelectKPP } from "../inputs/inputs";


class Step2 extends React.Component {
    constructor(props) {
        super(props)
        this.changeData = this.changeData.bind(this)
        this.filterClientGID = this.filterClientGID.bind(this)
        this.filterClientName = this.filterClientName.bind(this)
    }

    componentDidMount() {
        this.props.getClients();
        let urlIdTask = this.props.match.params.idtask;
        if (urlIdTask &&
            urlIdTask != this.props.currentTeask) this.props.getTask(urlIdTask)
    }

    filterClientGID(e) {
        if (e.key == 'Enter') {
            this.props.getClients(e.target.value);
        }
    }

    filterClientName(e) {
        if (e.key == 'Enter') {
            this.props.getClients(null, e.target.value);
        }
    }

    changeData(e) {
        this.props.changeDataAC({
            name: e.target.getAttribute('data-name'),
            data: [e.target.value]
        })
        this.props.setSelectedClientDataAC({
            id: e.target.value
        })
    }
    getContracts = (e) => {
        this.props.getContracts(e.target.value, this.props.TypeService)
    }
    getListservicesByGid = (e) => {
        if (this.props.statusUser == 'admin') {
            this.props.getListservicesByGid(e.target.value);
        }
    }
    touched = (name) => {
        //debugger
        return () => this.props.touchedAC(name)
    }

    validationRequired = (name) => {
        //debugger
        var find = !(this.props.errors.allInputs.findIndex(item => item == name) == -1)
        return find && this.props.errors[name].touched && this.props.errors[name].error
        
    }

    render() {
        let valClient='';
        if(this.props.ClientListSelected[0]){
            valClient=this.props.ClientListSelected[0].name
            valClient=valClient?valClient:'';
        }
        let arrClients = this.props.clientsList.map(item => <option key={item.id} value={item.id}>{item.name}</option>)

        let selectContract = this.props.selectContractFormToggle ? <SelectContractContainer /> : <></>;
        let addClientForm = this.props.addClientFormToggle ? <AddClientContainer /> : <></>;
        return (
            <div>
                <h5>.2 </h5>
                <hr />
                <div className="form-group">
                    <label><h6>  </h6></label>
                    <div className='row'>
                        <div className='col-6'>
                            <input type="text" className="form-control" placeholder='  GID' onKeyPress={this.filterClientGID}></ input>
                        </div>
                        <div className='col-6'>
                            <input type="text" className="form-control" placeholder='  '
                                onKeyPress={this.filterClientName}></ input>
                        </div>
                    </div>
                    <MySelectKPP {...this.props} name='MainCP' title='' size='10'
                        onChange={(e) => { this.changeData(e); this.getContracts(e); this.getListservicesByGid(e) }}
                        onBlur={this.touched}
                        validation={this.validationRequired}
                    >
                        {arrClients}
                    </ MySelectKPP>
                </div>
                <div className="form-group">
                    <label><h6> :</h6></label>
                    <input
                        type="text" className="form-control" data-idclient={this.props.MainCP[0]}
                        value={valClient} readOnly={true}></ input>
                </div>
                <button className='btn btn-light w-100' onClick={this.props.toggleAddClientFormAC}>   </button>
                {addClientForm}
                {selectContract}
            </div>
        )
    }
}

export default Step2