import Step5 from "./step5";
import { connect } from "react-redux";
import { withRouter } from "react-router";
import { compose } from "redux";
import { checkErrorsAC,getTask,changeDataAC,touchedAC,sendDataAC } from "../../../redux/reducers/reducer_taskDO";


let mapStateToProps = (state) => {
    //debugger;
    return {
        currentTeask: state.TaskDOForm.currentTeask,
        statusUser:state.TaskDOForm.statusUser,
        temporaryTaskDO: state.TaskDOForm.temporaryTaskDO,
        errors: state.TaskDOForm.errors
    }
}

let Step5Container = compose(
    connect(mapStateToProps, { checkErrorsAC,getTask,changeDataAC,touchedAC,sendDataAC }),
    withRouter)
    (Step5);
export default Step5Container