import React from "react";
import AddServiceForm from "../changs/addServiceContainer";
import style from "./step3.module.css";
import { MySelectKPP, MyInputKPP, MyChekBoxKPP, MyTextAreaKPP } from "../inputs/inputs";

class Step3 extends React.Component {
    constructor(props) {
        super(props)
        this.changeData = this.changeData.bind(this)
        this.addServicesListSelected = this.addServicesListSelected.bind(this)
        this.deleteServicesListSelected = this.deleteServicesListSelected.bind(this)
        this.defaultSubject = this.defaultSubject.bind(this)
        this.filterServices = this.filterServices.bind(this)
    }
    filterServices(e) {
        if (e.key == 'Enter') {
            this.props.getServices(e.target.value);
        }
    }
    defaultSubject(e) {
        e.preventDefault();
        this.props.defaultSubjectAC();
    }
    deleteServicesListSelected(e) {
        //console.log(e.target.getAttribute('data-id'));
        this.props.deleteServicesListSelectedAC({
            id: e.target.getAttribute('data-id')
        })
    }
    componentDidMount() {
        this.props.getPeriodsAct();
        this.props.getServices();
        this.props.getEOSZ();
        let urlIdTask = this.props.match.params.idtask;
        if (urlIdTask &&
            urlIdTask != this.props.currentTeask) this.props.getTask(urlIdTask)
    }
    addServicesListSelected(e) {
        this.props.addServicesListSelectedAC({
            id: e.target.value,
            data: e.target.value
        })
    }
    changeData(e) {
        this.props.changeDataAC({
            name: e.target.getAttribute('data-name'),
            data: e.target.value
        })
    }
    validationRequired = (name) => {
        //debugger
        var find = !(this.props.errors.allInputs.findIndex(item => item == name) == -1)
        return find && this.props.errors[name].touched && this.props.errors[name].error
    }
    touched = (name) => {
        //debugger
        return () => this.props.touchedAC(name)
    }

    render() {
        let addServiceForm = this.props.addServiceFormToggle ? <AddServiceForm /> : <></>;
        let peridsAct = this.props.periodsActList.map(item => <option key={item.id} value={item.id}>{item.name}</option>)
        let servicesList = this.props.servicesList.map(item => <option key={item.id} value={item.id}>{item.name}</option>)
        let selectedServices = this.props.servicesListSelected.map(item =>
            <p className='m-0'>{item.name}<span className='finger'
                data-id={item.id}
                onClick={this.deleteServicesListSelected}>&#10060;</span></p>)
        //console.log(this.props)
        let addBtn = <a href='#' className='float-right' onClick={this.defaultSubject}>  </ a>
        return (
            <div>
                <h5>.3 </h5>
                <hr />
                <MySelectKPP {...this.props} name='PerActService' title=' '
                    onChange={this.changeData}
                    onBlur={this.touched}
                    validation={this.validationRequired}>
                    {peridsAct}
                </MySelectKPP>

                <div className='row align-items-start mx-0'>
                    <div className='d-inline-block'>
                        <MyInputKPP {...this.props} type='date' name='DateStart' title='  '
                            onChange={this.changeData}
                            onBlur={this.touched}
                            validation={this.validationRequired} />
                    </div>
                    <div className='d-inline-block ml-2'>
                        <MyInputKPP {...this.props} type='date' name='DateEnd' title='  '
                            onChange={this.changeData}
                            onBlur={this.touched}
                            validation={this.validationRequired} />
                    </div>
                </div>

                <MyTextAreaKPP {...this.props} rows='3' name='DateText' title='  ( ,  )'
                    onChange={this.changeData}
                    onBlur={this.touched}
                    validation={this.validationRequired} />
                {/* <div className="form-group">
                    <label className='d-block'><h6>    :</h6></label>
                    <div className="form-group d-inline-block">
                        <label>  :</label>
                        <input type='date' className="form-control"
                            value={this.props.temporaryTaskDO.DateStart} data-name='DateStart' onChange={this.changeData}
                        ></input>
                    </div>
                    <div className="form-group d-inline-block ml-2">
                        <label>  :</label>
                        <input type='date' className="form-control"
                            value={this.props.temporaryTaskDO.DateEnd} data-name='DateEnd' onChange={this.changeData}
                        ></input>
                    </div>
                    <div className="form-group">
                        <label>  ( ,  ):</label>
                        <textarea rows='3' className="form-control"
                            value={this.props.temporaryTaskDO.DateText} data-name='DateText' onChange={this.changeData}
                        ></textarea>
                    </div>
                </div> */}
                <div className="form-group">
                    <label><h6> /</h6></label>
                    <div className='row'>
                        <div className='col-6'>
                            <input type="text" className="form-control" placeholder=' '
                                onKeyPress={this.filterServices}></ input>
                        </div>
                        <div className='col-6'>
                             
                    </div>
                    </div>
                    <div className={'row ' + style.heightrow}>
                        <div className='col-6 h-100'>
                            {/* <MySelectKPP {...this.props} size='5' name='PerActService' title=''
                                onChange={null}
                                onDoubleClick={this.addServicesListSelected}
                            >
                                {servicesList}
                            </MySelectKPP> */}
                            <select size='5' className="form-control h-100" onDoubleClick={this.addServicesListSelected}>
                                <option></option>
                                {servicesList}
                            </select>
                        </div>
                        <div className='col-6 h-100'>
                            <div className='h-100 overflow-auto'>
                                {selectedServices}
                            </div>
                        </div>
                    </div>
                    <button className='btn btn-light w-100' onClick={this.props.toggleAddServiceFormAC}>   </button>
                    {addServiceForm}
                </div>

                <MyTextAreaKPP {...this.props} addBtn={addBtn} name='SubjectContract' title=' ' rows="3"
                    onChange={this.changeData}
                    onBlur={this.touched}
                    validation={this.validationRequired} />
                <MyTextAreaKPP {...this.props} name='AdditionalConditional' title=' ' rows="3"
                    onChange={this.changeData}
                    onBlur={this.touched}
                    validation={this.validationRequired} />
            </div>
        )
    }
}

export default Step3