export function saveNewClient(state) {
    var jsonState = JSON.stringify(state)
    return dispatch =>
        myAxios({ newClient: jsonState }).then(data => {
            //console.log(data)
            dispatch({
                type: SAVENEWCLIENT,
                data: data[0]
            })
        }
        )
}
export function saveNewService(state) {
    var jsonState = JSON.stringify(state)
    return dispatch =>
        myAxios({ newService: jsonState }).then(data => {
            //console.log(data)
            dispatch({
                type: SAVENEWSERVICE,
                data: data[0]
            })
        }
        )
}
export function getListservicesByGid(gid) {
    //debugger;
    return dispatch => {
        myAxios({ getListservicesByGid: 'getListservicesByGid', gid: gid }).then(data => {
            //debugger;
            dispatch(setListservicesByGid(data))
        }
        )
    }
}
export function gotoWork(params) {
    //debugger;
    return dispatch => {
        dispatch({
            type: SETSTATUSTASK,
            data: 1
        });
        dispatch(checkErrorsAC())
        dispatch(sendDataAC())
    }
}
export function cancelTask(params) {
    //debugger;
    return dispatch => {
        dispatch({
            type: SETSTATUSTASK,
            data: -1
        });
        dispatch(checkErrorsAC())
        dispatch(sendDataAC())
    }
}
