function TaskDOReducer(state = initialState, action) {

    switch (action.type) {
        case RESETERRORS:
            //debugger
            var newObj = {}
            Object.keys(state.errors).forEach(item => {
                if (item != 'allInputs') {
                    newObj[item] = {
                        ...state.errors[item],
                        touched: false,
                        error: true
                    }
                }
            })
            console.log(state.errors)
            console.log({
                ...state,
                errors: {
                    ...state.errors,
                    totalError: false,
                    ...newObj
                }
            })
            return {
                ...state,
                errors: {
                    ...state.errors,
                    totalError: false,
                    ...newObj
                }
            }
        case SETSTATUSTASK:
            return {
                ...state,
                temporaryTaskDO: {
                    ...state.temporaryTaskDO,
                    statusTask: action.data
                }
            }
        case SENDDATA:
            //console.log(state.errors.totalError)
            //debugger
            if (!state.errors.totalError) {
                myAxios({ senddata: 'senddata', data: state.temporaryTaskDO }).then(data => {
                    return {
                        ...state,
                        temporaryTaskDO: { ...data }
                    }
                }
                )
            }
            return state
        case TOUCHEDAC:
            //debugger;
            return {
                ...state,
                errors: {
                    ...state.errors,
                    [action.name]: {
                        ...state.errors[action.name],
                        touched: true
                    }
                }
            }
        case SETLISTSERVICESBYGID:
            //debugger
            return {
                ...state,
                listServicesByGid: [...action.data]
            }
        case SETSTATUSUSER:
            return {
                ...state,
                statusUser: action.data
            }
        case SETCURRENTTEASK:
            return {
                ...state,
                currentTeask: action.data
            }
        case SETTASK:
            return {
                ...state,
                temporaryTaskDO: { ...action.data }
            }
        case CHANGEDATAFILTER: {
            return {
                ...state,
                filterListTask: {
                    ...state.filterListTask,
                    [action.data.name]: action.data.data
                }
            }
        }
        case GETLISTTASK:
            //debugger
            return {
                ...state,
                listTasks: [...action.data]
            }
        case SETCONTRACTS:
            return {
                ...state,
                contracts: [...action.data]
            }
        case PURENTDOCUMENT:
            return {
                ...state,
                temporaryTaskDO: {
                    ...state.temporaryTaskDO,
                    num_doc_parent: {
                        id: action.data.id,
                        number: action.data.number
                    }
                }
            }
        case SAVENEWSERVICE:
            //debugger;
            return {
                ...state,
                addService: { ...action.data },
                servicesListSelected: [...state.servicesListSelected,
                {
                    id: 0,
                    name: action.data.nameService
                }]
            };
        case SAVENEWCLIENT:
            return {
                ...state,
                addClient: { ...action.data },
                temporaryTaskDO: {
                    ...state.temporaryTaskDO,
                    MainCP: [...state.temporaryTaskDO.MainCP, action.data.gid]
                },
                ClientListSelected: [{
                    id: action.data.gid,
                    name: action.data.name
                }]
            };
        case CHECKERRORS:
            //debugger;
            var newState = {
                ...state,
                errors: { ...state.errors }
            }
            var totalError = false;
            let checkArr = [...state.errors.allInputs]
            checkArr.forEach(item => {
                if (!state.temporaryTaskDO[item]) {
                    newState.errors[item] = {
                        ...newState.errors[item],
                        error: true,
                        touched: true
                    }
                    totalError = true
                }
            })
            newState.errors.totalError = totalError;
            /* if(!state.temporaryTaskDO.TypeDoc)console.log('hello')
            console.log(state.temporaryTaskDO) */
            return newState;
        case GETSY:
            return {
                ...state,
                refSY: { ...action.data }
            };
        case CHANGEDATANEWSERVICE:
            return {
                ...state,
                addService: {
                    ...state.addService,
                    [action.name]: action.data
                }
            };
        case CHANGEDATANEWCLIENT:
            return {
                ...state,
                addClient: {
                    ...state.addClient,
                    [action.name]: action.data
                }
            };
        case CLEARREFSY:
            return {
                ...state,
                refSY: [{
                    ref: null,
                    name: null
                }]
            };
        case DEFAULTSUBJECT:
            var newSubject = '';
            state.servicesListSelected.forEach(item => newSubject += newSubject ? ", " + item.name : item.name)
            var errMark = newSubject ? false : true;
            return {
                ...state,
                temporaryTaskDO: {
                    ...state.temporaryTaskDO,
                    SubjectContract: newSubject
                },
                errors: {
                    ...state.errors,
                    SubjectContract: {
                        ...state.errors.SubjectContract,
                        error: errMark
                    }

                }
            };
        case DELETESERVICESLISTSELECTEDAC:
            var newState = {
                ...state,
                servicesListSelected: [...state.servicesListSelected]
            }
            var delid = newState.servicesListSelected.findIndex(item => item.id == action.id)
            newState.servicesListSelected.splice(delid, 1);
            return newState
        case ADDSERVICESLISTSELECTED:
            var elem = state.servicesList.find(item => item.id == action.data.id)
            return {
                ...state,
                servicesListSelected: [{ ...elem }, ...state.servicesListSelected],
                errors: {
                    ...state.errors,
                    Services: false
                }
            };
        case GETEOSZ:
            return {
                ...state,
                EOSZList: [...action.data]
            };
        case GETSERVICESLIST:
            return {
                ...state,
                servicesList: [...action.data]
            };
        case GETPERIODSACT:
            return {
                ...state,
                periodsActList: [...action.data]
            };
        case SETCLIENTS:
            return {
                ...state,
                clientsList: [...action.data]
            };
        case SETDATANULL:
            return {
                ...state,
                temporaryTaskDO: {
                    ...state.temporaryTaskDO,
                    Dep: ''
                }
            };
        case GETDEPARTMENTS:
            return {
                ...state,
                departmentsList: [...action.data]
            };
        case GETTYPESCONTRACT:
            return {
                ...state,
                typeContractsList: [...action.data]
            };
        case CLIENTLISTSELECTED:
            //debugger;
            var elem = state.clientsList.find(item => item.id == action.data.id)
            return {
                ...state,
                ClientListSelected: [{ ...elem }]
            };
        case RULESSTEP1AC:
            var OneOrVileTasks = state.temporaryTaskDO.TypeDoc == 2 ? 'one' : state.temporaryTaskDO.OneOrVileTasks;
            if (state.temporaryTaskDO.OneOrVileTasks == OneOrVileTasks) return state;

            return {
                ...state,
                temporaryTaskDO: {
                    ...state.temporaryTaskDO,
                    OneOrVileTasks: OneOrVileTasks
                }
            };
        case CHANGEDATA:
            //debugger;
            var errMark = action.data[0] ? false : true;
            return {
                ...state,
                temporaryTaskDO: {
                    ...state.temporaryTaskDO,
                    [action.name]: action.data
                },
                errors: {
                    ...state.errors,
                    [action.name]: {
                        ...state.errors[action.name],
                        error: errMark
                    }

                }
            };
        case CLEARDATA:
            //debugger;
            var errMark = false;
            return {
                ...state,
                temporaryTaskDO: {
                    ...state.temporaryTaskDO,
                    [action.name]: ''
                },
                errors: {
                    ...state.errors,
                    [action.name]: {
                        ...state.errors[action.name],
                        error: errMark
                    }

                }
            };
        case TOGGLEADDSERVICEFORM:
            //debugger;
            return {
                ...state,
                toggleForms: {
                    ...state.toggleForms,
                    addServiceForm: !state.toggleForms.addServiceForm
                }
            };
        case TOGGLEADDCLIENTFORM:
            //debugger;
            return {
                ...state,
                toggleForms: {
                    ...state.toggleForms,
                    addClientForm: !state.toggleForms.addClientForm
                }
            };
        case TOGGLESELECTCONTRACTFORM:
            //debugger;
            return {
                ...state,
                toggleForms: {
                    ...state.toggleForms,
                    selectContractForm: action.data
                }
            };
        default:
            return state;
    }
}
export default TaskDOReducer;