import React, { Component } from 'react';

class ListServicesByGid extends Component {
    componentDidMount() {
        this.props.getListservicesByGid(this.props.MainCP[0]);
    }
    render() {
        //console.log(this.props)
        let rows = this.props.listServicesByGid.map(item=><tr key={item.idDocument}>
            <th scope="row">{item.idDocument}</th>
            <td>{item.codeService}</td>
            <td>{item.numDoc}</td>
            <td>{item.statusDoc}</td>
            <td>{item.dateBegeen}</td>
            <td>{item.dateEnd}</td>
        </tr>) 
        return (
            <div>
                <h5>   </h5>
                <hr />
                <table className="table table-sm">
                    <thead>
                        <tr>
                            <th scope="col">ID </th>
                            <th scope="col"> </th>
                            <th scope="col"> </th>
                            <th scope="col"> </th>
                            <th scope="col">  </th>
                            <th scope="col">  </th>
                        </tr>
                    </thead>
                    <tbody>
                        {rows}
                    </tbody>
                </table>
            </div>
        );
    }
}

export default ListServicesByGid;