import React from "react";
import { component } from "react";
import reactDom from "react-dom";
import { NavLink } from "react-router-dom";




function TaskRow(props) {
    let color = props.idUser != props.dataTask.executor_id ? "bg-secondary" : "";
    let myRef = "/ReadTask/" + props.dataTask.id;
    let style = '';
    let simv = "";
    if (props.dataTask.statusTask == 1) simv = "\u{2705}";
    if (props.dataTask.statusTask == -1) simv = "\u{274C}";
    let saved = props.dataTask.saved ? "\u{2705}" : "\u{274C}";
    var dateBegeen = new Date(props.dataTask.dateBegeen);
    var m = dateBegeen.toLocaleString("ru", { month: '2-digit' });
    var d = dateBegeen.toLocaleString("ru", { day: '2-digit' });
    var y = dateBegeen.toLocaleString("ru", { year: 'numeric' });
    var dateBegeen = `${d}.${m}.${y}`;
    var dateEnd = new Date(props.dataTask.dateEnd);
    var m = dateEnd.toLocaleString("ru", { month: '2-digit' });
    var d = dateEnd.toLocaleString("ru", { day: '2-digit' });
    var y = dateEnd.toLocaleString("ru", { year: 'numeric' });
    var dateEnd = `${d}.${m}.${y}`;

    return (
        <tr className={color + ' ' + style} key={props.dataTask.id}>
            <td><NavLink to={myRef}>{props.dataTask.id}</NavLink></td>
            <td>{simv}</td>
            <td>{dateBegeen}</td>
            <td>{dateEnd}</td>
            <td>{props.dataTask.executor_name}</td>
            <td>{props.dataTask.task}</td>
            <td>{props.dataTask.tyteTask_name}</td>
            <td>{saved}</td>
        </tr>
    )

}

class ListTasks extends React.Component {
    constructor(props) {
        super(props);
    }
    componentDidMount() {
        if (this.props.updateList) this.props.updateList();
        //if (this.props.updateList) this.props.updateList();
        if (this.props.updateList) this.props.updateDirs();
    }

    render() {
        if (!this.props.dataTasks) {
            return <dir></dir>
        }
        let rows = this.props.dataTasks.map((item) =>
            <TaskRow dataTask={item} idUser='1' />
        );
        return (
            <div>
                <h5>{this.props.title}</h5>
                <table className="table">
                    <thead>
                        <tr key='-1'>
                            <th scope="col">id</th>
                            <th scope="col"></th>
                            <th scope="col"> </th>
                            <th scope="col"> </th>
                            <th scope="col"></th>
                            <th scope="col"> </th>
                            <th scope="col"> </th>
                            <th scope="col"></th>
                        </tr>
                    </thead>
                    <tbody>
                        {rows}
                    </tbody>
                </table>
            </div >
        )
    }
}
export default ListTasks