import React, { Component } from 'react';
import { Form, Field } from 'react-final-form'
import { MyInput, MyTextArea, MySelect } from "../inputs/inputs";


class AddServiceForm extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.props.getTypesContract()
        this.props.getDepartments()
    }

    onSubmit = values => {
        this.props.saveNewService(values)
    }
    render() {
        console.log(this.props)
        let typesContract = this.props.typeContractsList.map(item => <option key={item.id} value={item.id}>{item.name}</option>)
        let departmentsList = this.props.departmentsList.map(item => <option key={item.id} value={item.id}>{item.name}</option>)
        return (
            <Form
                onSubmit={this.onSubmit}
                validate={values => {
                    let requiredText = '   .';
                    const errors = {}
                    //console.log(Object.keys(values));
                    let requireditems = [
                        "department",
                        "groupService",
                        "manager",
                        "codeService",
                        "nameService",
                        "typeService",
                        "nomenclatureGroup",
                        "measurement"
                    ]
                    requireditems.forEach(item => {if(!values[item]) errors[item]=requiredText})
                    return errors
                }}
                initialValues={this.props.addService}
                render={({ handleSubmit, form, submitting, pristine, values }) => (
                    <form onSubmit={handleSubmit}>
                        <div className="form-group">
                            <label><h6 className='text-body'>,  :</h6></label>
                            <Field name="department" component={MySelect}>
                                <option value={null}></option>
                                {departmentsList}
                            </Field>
                        </div>
                        <div className="form-group">
                            <label><h6 className='text-body'> :</h6></label>
                            <Field name="groupService" type='text' component={MyInput} />
                        </div>
                        <div className="form-group">
                            <label><h6 className='text-body'> :</h6></label>
                            <Field name="manager" type='text' component={MyInput} />
                        </div>
                        <div className="form-group">
                            <label><h6 className='text-body'> /:</h6></label>
                            <Field name="codeService" type='text' component={MyInput} />
                        </div>
                        <div className="form-group">
                            <label><h6 className='text-body'> :</h6></label>
                            <Field name="nameService" rows="3" component={MyTextArea} />
                        </div>
                        <div className="form-group">
                            <label><h6 className='text-body'> :</h6></label>
                            <Field name="typeService" component={MySelect}>
                                <option value={null}></option>
                                {typesContract}
                            </Field>
                        </div>
                        <div className="form-group">
                            <label><h6 className='text-body'> :</h6></label>
                            <Field name="nomenclatureGroup" type='text' component={MyInput} />
                        </div>
                        <div className="form-group">
                            <label><h6 className='text-body'>  :</h6></label>
                            <Field name="measurement" type='text' component={MyInput} />
                        </div>
                        <button className='btn btn-primary w-100' disabled={submitting}></button>
                    </form>
                )}
            />
        );
    }
}

export default AddServiceForm;